.installment-pay {
  overflow: hidden;
  max-height: 0;                 /* collapsed */
  opacity: 0;
  transform: translateY(-40px);
  transition: max-height 1500ms ease, opacity 1500ms ease, transform 1000ms ease;
}
.installment-pay.show {
  /* enough to contain the row */
  max-height: max-content; /* or 200px; */
  opacity: 1;
  transform: translateY(0);
}

/* ################################################# */


/* Toggle switch style */
.toggle-switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 28px;
}

.toggle-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  /* border-radius: 28px; */
  border-radius: 5px;
}

.slider:before {
  position: absolute;
  content: "";
  height: var(--before-height, 24px);
  width: 24px;
  left: 2px;
  bottom: 2px;
  /* height: 20px; */
  /* width: 20px; */
  /* left: 4px; */
  /* bottom: 4px; */
  background-color: white;
  transition: 0.4s;
  /* border-radius: 50%; */
  border-radius: 4px;
  /* border-top-right-radius: 4px;
  border-bottom-right-radius: 4px; */
}

.toggle-switch input:checked + .slider {
  background-color: #475569;
}

/* When the input is disabled, style the slider track */
.toggle-switch input:disabled + .slider {
  background-color: #ccc;
  cursor: not-allowed;
}

/* When the input is disabled, style the slider knob */
.toggle-switch input:disabled + .slider:before {
  /* background-color: #6C757D; */
  background-color: #4755696c;
}

.toggle-switch input:checked + .slider:before {
  transform: translateX(22px);
}

/* Flip animation */
.flip-container {
  perspective: 1000px;
  width: 100%;
  /* max-width: 400px; */
  /* margin-top: 1rem; */
}

.flipper {
  position: relative;
  width: 100%;
  transition: transform 0.6s;
  transform-style: preserve-3d;

  /* min-height: 700px; */
}

.flip-container.flipped .flipper {
  transform: rotateY(180deg);
}

.front,
.back {
  position: absolute;
  top: 0;
  left: 0;
  backface-visibility: hidden;
  /* width: 100%; */
  /* min-height: 150px; */
  /* padding: 1rem; */
  /* background: #f8f9fa; */
  /* border: 1px solid #ddd; */
  /* border-radius: 6px; */
  display: flex;
  align-items: center;
  /* justify-content: center; */
}

.back {
  transform: rotateY(180deg);
}



/* ################################################# */
button:focus-visible,
div:focus-visible,
input:focus-visible,
select:focus-visible,
textarea:focus-visible,
a:focus-visible {
  outline: 1px solid #475569;
  outline-offset: -3px;
}


/* ################################################# */
/* Base wrapper for smooth slide specific store slides */
.store-container,
.specific-store {
  overflow: hidden;
  transition: all 0.4s ease;
  transform-origin: top;
  will-change: transform, opacity;
}

/* Open animation */
.specific-store.slideDownSpecificStore {
  transform: scaleY(1);
  opacity: 1;
}

/* Closed animation */
.specific-store.slideUpSpecificStore {
  transform: scaleY(0);
  opacity: 0;
  height: 0 !important;
  margin: 0 !important;
  padding: 0 !important;
}

.shipped-color {
  color: #475569;
}
.delivered-color {
  color: #22c55e;
}
.cancelled-color {
  color: #dc2626;
}
.processing-color {
  color: #eab308;
}

.expandable-container {
  position: relative;
  margin-left: 12px;
  padding-left: 10px;
  border-left: 2px solid rgba(200, 200, 200, 0.4);
}

.expandable-container:hover {
  border-left-color: rgba(150, 150, 150, 0.8);
}

.expandable-label {
  cursor: pointer;
  user-select: none;
  display: flex;
  align-items: center;
  gap: 4px;
}